.App {
  text-align: center;
  margin: auto 0;
}
.bg-blue{
  background-color: rgba(8, 86, 195, 0.918);
}
/*nav*/
.main-nav{
  list-style: none;
  margin: 0;
  box-shadow: 2px 2px 2.5px #a37c7c;
  text-align: center;
}
.sticky{
  position: fixed;
  top: 0;
  width: 100%;
}
a{
  color: white;
  text-decoration: none;
  font-size: 2em;
}
/*cover */
.cover{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}
.cover-text{
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translate(-50% , -50%); 
  font-size: 1.8em;
}
.link{
  font-size: 100%;
}
button{
  color: white;
  cursor: pointer;
  margin: 0;
  border: 0;
  transition: 0.4s;
  border-radius: 0.7em;
  padding: 0.2em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
}
 .home-btn{
  font-size: 1.5em;
  width: 30%;
  background-color:rgba(8, 86, 195, 0.918); 
  position: absolute;
  left: 50%;
  top: 66%;
  transform: translate(-50% , -50%); 
  box-shadow: 1px 1px 1.5px #888888;
 }
 .home-btn:hover{
    background-color: white;
    color: rgba(8, 86, 195, 0.918);  
    border:rgba(8, 86, 195, 0.918) 1px solid;    
    box-shadow: 2px 2px 3px #888888;
   }

 .home-logo{
   height: 10rem;
   position: absolute;
   left: 50%;
   top: 43%;
   transform: translate(-50% , -50%); 
 }

 /* quiz main */
 .question-text{
   font-size: 1.3em;
   font-weight: bold;
   color: white;
   padding: 0.2em;
 }
 .option-btn{
   width: 80%;
   font-size: 1.2em;
   background-color: white;
   color: rgba(8, 86, 195, 0.918); 
   border: rgba(8, 86, 195, 0.918) 1px solid;  
 }
 .option-btn:hover{
   font-size: 1.25em;
   width: 83%;
 }
 .clicked{
  width: 87%;
  font-size: 1.25em;
  background-color: rgba(8, 86, 195, 0.918);
  color: white; 
  border: rgba(8, 86, 195, 0.918) 1px solid;  
 }

.container{
  width: 220px;
  padding: 0.7rem;
  box-shadow: 1.5px 1px #888888;
  border: rgba(8, 86, 195, 0.918) 1px solid;
  background-color:rgba(8, 86, 195, 0.918) ;
  text-align: center;
  position: absolute;
   left: 50%;
   top: 40%;
   transform: translate(-50% , -50%); 
}
.quiz-logo{
  max-width: 40%;
  transition-duration: 1.2s ;
  transition-property: transform;
}
.quiz-logo:hover{
  max-width: 45%;
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}

.score-sec{
  font-size: 180%;
  color: white;
  font-weight: bold;
}